define([ "require", "stjs", "as/dto/common/search/AbstractSearchCriteria", "as/dto/datastore/search/DataStoreKind"],
	function(require, stjs, AbstractSearchCriteria, DataStoreKind) {

	var DataStoreKindSearchCriteria = function() {
		this.dataStoreKinds = [DataStoreKind.DSS];
	};

	stjs.extend(DataStoreKindSearchCriteria, AbstractSearchCriteria, [ AbstractSearchCriteria ], function(constructor, prototype) {
		prototype['@type'] = 'as.dto.datastore.search.DataStoreKindSearchCriteria';
		constructor.serialVersionUID = 1;
		prototype.dataStoreKinds = null;

		prototype.thatIn = function(dataStoreKinds) {
			this.dataStoreKinds = dataStoreKinds;
		};

		prototype.getDataStoreKinds = function() {
			return this.dataStoreKinds;
		};
	}, {
		dataStoreKinds: {
			name: "DataStoreKind[]"
		}
	});
	return DataStoreKindSearchCriteria;
})